* {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}
.mpu {
  outline: 2px solid #eee;
  height: 250px;
  width: 300px;
  position: fixed;
  top: 0;
  left: 0;
}
.banner {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 300px;
  height: 250px;
}

.banner .banner-img {
  width: 300px;
  height: 250px;
}

.banner .add {
  position: absolute;
  z-index: 2;
  top: 200px;
  left: 120px;
  width: 175px;
  height: 44px;
  opacity: 0;
  font-size: 0;
  border: none;
}

.retailers {
  position: fixed;
  left: 0;
  top: 250px;
  width: 300px;
  height: 250px;
  text-align: center;
  opacity: 0;
  z-index: 10;
  background-color: white;
  transition: all 0.5s ease-in-out
}

.retailers.active {
  -webkit-transform: translate(0, -250px);
          transform: translate(0, -250px);
  opacity: 1;
} 

.retailers .heading {
  font-size: 18px;
  font-weight: 700;
  width: 75%;
  color: #999999;
  margin-left: 45px;
  margin-top: 40px;
}

.retailers .back {
  font-size: 0;
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  left: 6px;
  top: 8px;
  border: none;
  background: 0 0;
  padding: 0;
  cursor: pointer;
}

.retailers .back > .fas {
  font-size: 20px;
  color: #999;
}

.retailers .retailer-button {
  margin: 10px 0 0 0;
  padding: 0;
  border: 2px solid #f5959e;
  width: 75px;
  height: 44px;
  background-color: #fff;
  background-size: auto 37px;
  background-position: center center;
  position: relative;
  cursor: pointer;
  outline: 0;
}


.retailers .retailer-button .retailer-logo {
  background-image: url("https://s3-eu-west-1.amazonaws.com/retailer-api/retailers/5676e8ec-2571-4444-aaf9-847085ef8aa2_2x.png");
  vertical-align: middle;
  width: 63px;
  height: 20px;
  background-color: transparent;
  background-position: left top;
  background-size: 63px auto;
  display: inline-block;
  font-size: 0;
  background-repeat: no-repeat;
}

.retailers .account {
  margin-bottom: 0;
  font-size: 14px;
  color: #999999;
  margin-top: 40px;
}

.retailers .privacy {
  margin-bottom: 10px;
  font-size: 12px;
  color: #999;
  font-weight: 700;
  text-decoration: underline;
  margin-top: 0;
}
.products-container {
  position: fixed;
  top: 0;
  left: 300px;
  z-index: 20;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}

.products-container.active {
  -webkit-transform: translate(-300px, 0);
          transform: translate(-300px, 0);
  opacity: 1;
}

.products {
  width: 300px;
  height: 250px;
  display: flex;
  flex-direction: column;
  background-color: white;
}

.products .price {
  line-height: 22px;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  color: #999999;
  margin: 0 0 3px;
}

.products .product-carousel-container .offer {
  position: absolute;
  width: 300px;
  z-index: 24;
  background-color: #90232d;
  color: hsla(0, 0%, 100%, 0.8);
  text-align: center;
  font-size: 12px;
  line-height: 1.8em;
  margin-top: -35px;
}
.footer {
  display: flex;
  min-height: 30px;
}

.footer .basket {
  flex-basis: 33.33%;
  background-color: #f53447;
  border: none;
  cursor: pointer;
  color: white;
}

.footer .basket .fas {
  position: fixed;
  top: 224px;
  left: 6px;
}

.footer .add {
  flex-basis: 33.33%;
  background-color: #dc1528;
  border: none;
  color: white;
  cursor: pointer;
}

.footer .add .fas {
  position: fixed;
  top: 224px;
  left: 174px;
}

.footer .next {
  flex-basis: 33.33%;
  background-color: #90232d;
  border: none;
  cursor: pointer;
}

.footer .next .retailer-logo {
  background-image: url("https://s3-eu-west-1.amazonaws.com/retailer-api/retailers/5676e8ec-2571-4444-aaf9-847085ef8aa2_2x.png");
  -webkit-filter: brightness(0) invert(1);
          filter: brightness(0) invert(1);
  vertical-align: middle;
  width: 63px;
  height: 20px;
  background-color: transparent;
  background-position: left top;
  background-size: 63px auto;
  display: inline-block;
  font-size: 0;
  background-repeat: no-repeat;
}

.footer .next .fas {
  display: inline-block;
  font-size: 16px;
  color: white;
  vertical-align: middle;
  margin-left: 5px;
}

.header {
  min-height: 30px;
  display: flex;
}

.header .back {
  font-size: 0;
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  left: 6px;
  top: 8px;
  z-index: 24;
  border: none;
  background: 0 0;
  padding: 0;
  cursor: pointer;
}

.header .back > .fas {
  font-size: 20px;
  color: #999;
}
.header .title {
  font-size: 12px;
  position: absolute;
  text-align: center;
  width: 240px;
  margin-left: 35px;
  z-index: 24;
  color: #999999;
  font-weight: 400;
}


.product-carousel-container {
  height: 160px;
  width: 100%;
  position: relative;
}

.product-carousel-container .previous {
  font-size: 0;
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  left: 0;
  top: 40px;
  z-index: 24;
  border: none;
  background: 0 0;
  padding: 0;
  cursor: pointer;
}

.product-carousel-container .next {
  width: 30px;
  height: 30px;
  position: absolute;
  left: 270px;
  top: 40px;
  z-index: 24;
  border: none;
  background: 0 0;
  padding: 0;
  cursor: pointer;
}

.product-carousel-container .previous > .fas, .product-carousel-container .next > .fas {
  color: #dc1528;
  font-size: 24px;
}

.carousel {
  height: 160px;
  width: 300px;
  position: relative;
}

.product-carousel-container .product-image {
  width: 160px;
  height: 160px;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  margin: 0 auto;
  background: 0 0;
  -webkit-transform-origin: 50% 0;
          transform-origin: 50% 0;
  opacity: 1;
}

.product-carousel-container .carousel.move-right .product-image,
.product-carousel-container .carousel.move-left .product-image {
  transition: .5s all ease-in-out;
}

.product-carousel-container .product-image > img {
  max-width: 160px;
  max-height: 160px;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

/* FAR LEFT PRODUCT */
.product-carousel-container .product-image.far-left-product {
  -webkit-transform: scale(.75) translate(-186.67px,0);
          transform: scale(.75) translate(-186.67px,0);
  z-index: 21;
  opacity: 0;
}

.product-carousel-container .carousel.move-left .product-image.far-left-product {
  -webkit-transform: scale(.75) translate(-346.67px,0);
          transform: scale(.75) translate(-346.67px,0);
  z-index: 21;
  opacity: 0;
}

.product-carousel-container .carousel.move-right .product-image.far-left-product {
  -webkit-transform: scale(.75) translate(-21.67px,0);
          transform: scale(.75) translate(-21.67px,0);
  z-index: 22;
  opacity: .7;
}

/* LEFT PRODUCT */
.product-carousel-container .product-image.left-product {
  -webkit-transform: scale(.75) translate(-21.67px,0);
          transform: scale(.75) translate(-21.67px,0);
  z-index: 22;
  opacity: .7;
}

.product-carousel-container .carousel.move-left .product-image.left-product {
  -webkit-transform: scale(.75) translate(-186.67px,0);
          transform: scale(.75) translate(-186.67px,0);
  z-index: 21;
  opacity: 0;
}

.product-carousel-container .carousel.move-right .product-image.left-product {
  -webkit-transform: scale(1) translate(70px,0);
          transform: scale(1) translate(70px,0);
  z-index: 23;
  opacity: 1;
}

/* PRIMARY PRODUCT */
.product-carousel-container .product-image.primary-product {
  -webkit-transform: scale(1) translate(70px,0);
          transform: scale(1) translate(70px,0);
  z-index: 23;
  opacity: 1;
}

.product-carousel-container .carousel.move-left .product-image.primary-product {
  -webkit-transform: scale(.75) translate(-21.67px,0);
          transform: scale(.75) translate(-21.67px,0);
  z-index: 22;
  opacity: .7;
}

.product-carousel-container .carousel.move-right .product-image.primary-product {
  -webkit-transform: scale(.75) translate(208.33px,0);
          transform: scale(.75) translate(208.33px,0);
  z-index: 22;
  opacity: .7;
}

/* RIGHT PRODUCT */
.product-carousel-container .product-image.right-product {
  -webkit-transform: scale(.75) translate(208.33px,0);
          transform: scale(.75) translate(208.33px,0);
  z-index: 22;
  opacity: .7;
}

.product-carousel-container .carousel.move-left .product-image.right-product {
  -webkit-transform: scale(1) translate(70px,0);
          transform: scale(1) translate(70px,0);
  z-index: 23;
  opacity: 1;
}

.product-carousel-container .carousel.move-right .product-image.right-product {
  -webkit-transform: scale(.75) translate(373.33px,0);
          transform: scale(.75) translate(373.33px,0);
  z-index: 21;
  opacity: 0;
}

/* FAR-RIGHT PRODUCT */
.product-carousel-container .product-image.far-right-product {
  -webkit-transform: scale(.75) translate(373.33px,0);
          transform: scale(.75) translate(373.33px,0);
  z-index: 21;
  opacity: 0;
}

.product-carousel-container .carousel.move-left .product-image.far-right-product {
  -webkit-transform: scale(.75) translate(208.33px,0);
          transform: scale(.75) translate(208.33px,0);
  z-index: 22;
  opacity: .7;
}

.product-carousel-container .carousel.move-right .product-image.far-right-product {
  -webkit-transform: scale(.75) translate(533.33px,0);
          transform: scale(.75) translate(533.33px,0);
  z-index: 21;
  opacity: 0;
}
.basket-overlay {
  position: fixed;
  top: 250px;
  left: 0;
  height: 250px;
  width: 300px;
  z-index: 25;
  opacity: 0;
  background-color: white;
  transition: all 0.5s ease-in-out;
}

.basket-overlay.active {
  -webkit-transform: translate(0, -250px);
          transform: translate(0, -250px);
  opacity: 1;
} 

.basket-overlay .total {
  font-size: 16px;
  text-align: center;
  padding-top: 40px;
  color: #afafaf;
}

.basket-overlay .back {
  font-size: 0;
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  left: 6px;
  top: 8px;
  z-index: 26;
  border: none;
  background: 0 0;
  padding: 0;
  cursor: pointer;
}

.basket-overlay .back > .fas {
  color: #999999;
  font-size: 16px;
}

.basket-overlay .basket-table {
  margin: 0 10px;
}

.basket-overlay .basket-table > tbody {
  vertical-align: top;
}

.basket-overlay .basket-table .remove > button{
  color: #de2222;
  background: 0;
  border: none;
  cursor: pointer;
}

.basket-overlay .basket-table .title {
  font-size: 12px;
  color: #999999
}

.basket-overlay .basket-table .quantity {
  font-size: 12px;
  font-weight: 700;
  padding: 0px 25px;
  color: #afafaf
}

.basket-overlay .basket-table .total-price {
  font-size: 12px;
  font-weight: 700;
  color: #afafaf
}
